/* 
--------------------------------------------------------- 
                    DEFAULT SETTINGS 
--------------------------------------------------------- 
*/

/* Imports  */
@import url('https://fonts.googleapis.com/css2?family=Overpass:wght@300;400;500;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Rozha+One&display=swap');

/* Removing default stylings */
* {
    margin: 0; 
    padding: 0; 
    border: 0; 
    outline: 0; 
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}


/* CSS Root Variables */
:root {
    --color-bg: #425854; 
    --color-bg-variant: #5E887F; 
    --color-primary: #C5D5D2; 
    --color-primary-variant: #CC926B;
    --color-white: #fff; 
    --color-light: #EFD2C2;

    --container-width-lg: 75%; 
    --container-width-md: 86%; 
    --container-width-sm: 90%; 

    --transition: all 400ms ease; 
}


/* Scrolling Settings */
html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}


body {
    font-family: 'Overpass', sans-serif;
    background-color: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    /* background-image: url(./assets/bg-texture.png); */
}


/* 
--------------------------------------------------------- 
                    GENERAL STYLES
--------------------------------------------------------- 
*/
.container {
    width: var(--container-width-lg); 
    margin: 0 auto;  /* middle horizontal */
}


/* Text */
h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-family: 'Rozha One', serif;
    font-size: 3.5rem;
}

section {
    margin-top: 8rem; 
    /* height: 100vh; */
}

section > h2, section > h5 {
    text-align: cetner;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary); 
    margin-bottom: 3rem; 
}

.text-light {
    color: var(--color-light); 
}

a {
    color: var(--color-primary); 
    transition: var(--transition);
}

a:hover {
    color: var(--color-white); 
}


/* Buttons */
.btn {
    width: max-content; 
    display: inline-block; 
    color: var(--color-primary); 
    padding: 0.75rem 1.2rem; 
    border-radius: 0.4rem;
    cursor: pointer; 
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background: var(--color-white); 
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary); 
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%; 
    object-fit: cover;
}



/* 
--------------------------------------------------------- 
                    MEDIA QUERIES
--------------------------------------------------------- 
*/
/* -------------------- TABLET -------------------- */
@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-md);
    }

    h1 {
        font-size: 3rem;
    }

    section {
        margin-top: 6rem; 
    }
}


/* -------------------- PHONE -------------------- */
@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-sm);
    }

    h1 {
        font-size: 2.8rem;
    }

    section > h2{
        margin-bottom: 2rem; 
    }
}