/* 
--------------------------------------------------------- 
                    ABOUT ME CSS 
--------------------------------------------------------- 
*/
@import url('https://fonts.googleapis.com/css2?family=Rozha+One&display=swap');

/* -------------------- SECTION -------------------- */
.about_container {
    display: grid; 
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about_me {
    width: 100%; 
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: grid; 
    place-items: center;
}

.about_me_title {
    font-family: 'Rozha One', serif;
    text-align: center;
    font-size: 3rem;
    color: var(--color-white);
}

/* -------------------- IMAGE -------------------- */

.about_me_image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about_me_image:hover {
    transform: rotate(0);
}

/* -------------------- CARD -------------------- */
.about_cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem; 
}

.about_card {
    background: var(--color-bg-variant);
    border: 1px solid transparent; 
    border-radius: 1rem;
    padding: 2rem; 
    text-align: center;
    transition: var(--transition);
}

.about_card:hover {
    background: transparent;
    border: 1px solid var(--color-bg-variant);
}

.about_icon {
    color: var(--color-primary);
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.about_card h5 {
    font-size: 0.95rem;
}

.about_card small {
    font-size: 0.7rem;
    color: var(--color-light);
}

.about_content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}



/* 
--------------------------------------------------------- 
                    MEDIA QUERIES
--------------------------------------------------------- 
*/

/* -------------------- TABLET -------------------- */
@media screen and (max-width: 1024px) {
    .about_container {
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about_me {
        width: 50%; 
        margin: 2rem auto 4rem;
    }

    .about_content p {
        margin: 1rem 0 1.5rem;
    }
}


/* -------------------- PHONE -------------------- */
@media screen and (max-width: 600px) {
    .about_me {
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about_cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }
    .about_content {
        text-align: center;
    }
    .about_content p {
        margin: 1.5rem 0;
    }
}


