nav {
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    display: block;
    padding: 0.5rem 1.3rem;
    z-index: 1; 
    position: fixed; 
    left: 50%;
    transform: translateX(-50%);
    bottom: 1rem; 
    display: flex; 
    gap: 0.8rem; 
    border-radius: 3rem;
    backdrop-filter: blur(5px);
}

nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex; 
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover {
    background: rgba(0, 0, 0, 0.3);
}

nav a.active {
    background: var(--color-primary);
    color: var(--color-bg);
}